.hidden {
  display: none;
}

.search-form {
  background-color: #f4f4f4;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.search-form input[type="text"] {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #0056b3;
}

#gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px;
  display: flex;
}

#gallery-container {
  position: relative;
}

.photo-card {
  width: calc(25% - 25px);
  height: 300px;
  border-radius: 5px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photo-card .info {
  width: 100%;
  color: #fff;
  opacity: 0;
  background-color: rgba(0, 0, 0, .5);
  padding: 10px 10px 20px 15px;
  transition: opacity .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.photo-card:hover .info {
  opacity: 1;
}

.photo-card .info p {
  margin: 5px 0;
}

.bottom-button {
  z-index: 999;
  width: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, .5);
  border-radius: 5px;
  padding: 15px;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 4px rgba(0, 0, 0, .01);
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .4s, opacity .4s;
  animation: fadeIn .6s;
}

.load-more:hover {
  background-color: #0056b3;
}

@media screen and (max-width: 768px) {
  .photo-card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 480px) {
  .photo-card {
    width: calc(100% - 20px);
  }
}

